import React, { useState, useRef } from "react";
import Chevron from "./Chevron";

import "./Accordion.css";

function Accordion(props) {
  const [setActive, setActiveState] = useState("");
  const [setHeight, setHeightState] = useState("0px");
  const [setRotate, setRotateState] = useState("accordion__icon");

  const content = useRef(null);

  function toggleAccordion() {
    setActiveState(setActive === "" ? "active" : "");
    setHeightState(
      setActive === "active" ? "0px" : `${content.current.scrollHeight}px`
    );
    setRotateState(
      setActive === "active" ? "accordion__icon" : "accordion__icon rotate"
    );
  }

  return (
    <div className="accordion__section">
      <button className={`accordion ${setActive}`} onClick={toggleAccordion} style={{ fontFamily: 'BalooBold', color: "#261919", fontSize: "22px", lineHeight: "50px", backgroundColor: "rgba(0, 0, 0, 0.0)" }} variant="text" color="default">
        <p style={{ padding: "0", margin: "0" }}  className="accordion__title">{props.title}</p>
        <Chevron className={`${setRotate}`} width={"20px"} height={"20px"} fill={"#EEE"} />
      </button>
      <div ref={content} style={{ maxHeight: `${setHeight}` }} className="accordion__content" >
        <div
          className="accordion__text"
          dangerouslySetInnerHTML={{ __html: props.content }}
        />
      </div>
    </div>
  );
}

export default Accordion;
