import styled from "styled-components";

export const BlockContainer = styled("section")`
  position: relative;
  width: 100%;
  height: 100%;
  
  margin-right: auto;
  margin-left: auto;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  background-color: #FCFCA4;

  @media only screen and (max-width: 1024px) {
    padding:  1rem 1rem 5rem 1rem;
  }
  @media only screen and (max-width: 768px) {
    padding-bottom: 5rem;
  }
`;

export const ContentContainer = styled("section")`
  width: 100%;
  padding: 5% 10% 10% 10%;

  @media only screen and (max-width: 992px) {
    padding: 10% 5% 10% 5%;
  }
  @media only screen and (max-width: 768px) {
    padding: 10% 1% 20% 1%;
  }
  @media only screen and (max-width: 576px) {
    padding: 15% 0 25% 0;
  }
`;

export const ContentWrapperTitle = styled("div")`
  padding: 2rem 0;
  display: flex;
  justify-content: center;
`;

export const FaqContainer = styled("div")`
  margin: 2rem 0;
  padding: 1rem 2rem;
  background-color: #B0514F;
  border-radius: 30px;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 30%);

  @media only screen and (max-width: 576px) {
    padding: 1rem 0.5rem;
  }
`;

export const Title = styled("section")`
  color: #111;
  font-family: BalooBold;
  font-size: 100px;
  text-shadow: 4px 4px 4px rgba(200, 200, 200, 30%);
`;

export const Wave = styled("section")`
  position: absolute;
  width: 100%;
  height: 100px;
  bottom: -1px;
  left: 0;
  z-index: 4;

  @media only screen and (max-width: 992px) {
    height: 80px;
  }
  @media only screen and (max-width: 576px) {
    height: 50px;
  }
`;