import Accordion from "./Accordion";

import {
  BlockContainer, 
  ContentContainer,
  ContentWrapperTitle,
  FaqContainer,
  Wave,
  Title,
} from "./styles";



const Faq = () => {

  return (
    <BlockContainer >

      <ContentContainer>
        
        <ContentWrapperTitle>
            <Title>FAQs</Title>
        </ContentWrapperTitle>


        <FaqContainer>
          <Accordion
            title="What are Teddy Bears Club ?"
            content="
            <p>
            1,111 unique, algorithmically generated Teddy Bears - stored with proof of ownership stored on the Solana blockchain.
            </p>
            "
          />
        </FaqContainer>   

        <FaqContainer>
          <Accordion
            title="What is the total supply ?"
            content="
            <p>
            There will be 1,111 Teddy Bears from over 50 attributes - every Teddy Bear will be unique. There will never be more than 1,111 Teddy Bears. 
            </p>
            "
          />
        </FaqContainer>   

        <FaqContainer>
          <Accordion
            title="What's the mint price ?"
            content="
            <p>
            0.45 $SOL for public sale.
            </p>
            "
          />
        </FaqContainer>   

        <FaqContainer>
          <Accordion
            title="When & where it will be released ? "
            content="
            <p>
            You can adopt a Teddy Bear on December 10, 2021 at teddybearsclub.co. You will need 0.45 $SOL for the mint.
            </p>
            "
          />
        </FaqContainer>   

        <FaqContainer>
          <Accordion
            title="Is there a secondary market ?"
            content="
            <p>
            We are already in contact with several marketplaces, but there will be more information about this a few days before the mint.
            </p>
            "
          />
        </FaqContainer>   

        <FaqContainer>
          <Accordion
            title="Need more information ?"
            content="
            <p>
            If you need more information, do not hesitate to send a message to a staff member or directly in the general section on discord/twitter.
            </p>
            "
          />
        </FaqContainer>   

      </ContentContainer>

      <Wave>
        <svg viewBox="0 0 500 80" preserveAspectRatio="none" style={{height: "100%", width: "100%", bottom: "-1px", left: "0", display: "block" }} >
          <path d="M0.00,49.98 C150.00,150.00 349.20,-50.00 500.00,49.98 L500.00,150.00 L0.00,190.00 Z" style={{stroke: "none", fill: "#111", bottom: "0"}} />
        </svg>
      </Wave>

    </BlockContainer>
  );
};

export default (Faq);
